.text-animate {
  display: contents;
  opacity: 0;
  animation: bounceIn;
  animation-duration: 1s;
  animation-delay: 1s;
  animation-fill-mode: forwards;
  min-width: 10px;
}

.text-animate-hover {
  min-width: 10px;
  display: contents;
  animation-fill-mode: both;
}
.text-animate-hover:hover {
  animation: rubberBand 1s;
  color: #ffd700;
}

